:host {
  --tp-z-index: 1000;
  --tp-background: var(--tp-color-neutral-1);

  display: block;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: var(--tp-z-index);

  .offline {
    background: var(--tp-background);
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;

    &__content {
      pointer-events: none;
      width: 100%;
      height: calc(100% - 50px);
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }

    &__icon {
      width: 48px;
      height: 48px;
      margin-bottom: 24px;
      color: var(--tp-color-neutral-10);
    }

    &__title {
      display: block;
      margin-bottom: 8px;
    }
  }
}