import {
  Component,
  Event,
  EventEmitter,
  getAssetPath,
  h,
  Host,
  Prop,
} from '@stencil/core';

@Component({
  tag: 'tp-offline',
  styleUrl: 'offline.scss',
  assetsDirs: ['assets'],
  scoped: true,
})
export class TpOffline {
  private iconLeft = getAssetPath('./assets/tp-chevron-left.svg');
  private iconOffline = getAssetPath('./assets/tp-wi-fi-off-2.svg');

  /**
   * Define o title do componente
   */
  @Prop({ reflect: true }) offlineTitle: string;

  /**
   * Define o title do componente
   */
  @Prop({ reflect: true }) offlineText: string;

  /**
   * todo
   */
  @Event() tpClick!: EventEmitter<void>;

  private onClick() {
    this.tpClick.emit();
  }

  render() {
    const { iconLeft, iconOffline, offlineTitle, offlineText, onClick } = this;

    return (
      <Host from-stencil>
        <div class="offline">
          <tp-header>
            <tp-navbar name="transparent" slot="navbar">
              <ion-button
                fill="clear"
                // @ts-ignore
                size="sm"
                slot="left"
                onClick={() => onClick()}
              >
                <ion-icon slot="icon-only" src={iconLeft}></ion-icon>
              </ion-button>
            </tp-navbar>
          </tp-header>

          <div class="offline__content">
            <ion-icon class="offline__icon" src={iconOffline}></ion-icon>

            <ion-text tp-type="p16b">
              <p class="offline__title">{offlineTitle}</p>
            </ion-text>

            <ion-text>
              <p tp-type="p16">{offlineText}</p>
            </ion-text>
          </div>
        </div>
      </Host>
    );
  }
}
